import Timer from "../components/Timer";

function Task5CH() {
  return (
    <div id="mainContent">
      <div className="row1">
        <div className="instruction" id="speakingInstructions">
          <p>
            Your family is relocating to another area, and you are looking for a
            new home there. You found two suitable options. Using the pictures
            and information below, choose the option that you prefer. In the
            next section, you will need to persuade a family member that your
            choice is the better choice.
          </p>
          <p>
            If you do not choose an option, the computer will choose one for
            you.
            <b>You do not need to speak for this part. </b>
          </p>
        </div>
        <div className="hidden" id="stageTwoText">
          Your family is suggesting another house. Persuade your family member
          that what you chose is more suitable by comparing the two.
        </div>
      </div>
      <br />
      <div id="comparePersuadeQuestionBody">
        <div id="leftSpeakingPicture" className="wellWhiteBG pointerCurser">
          <img
            src="data:image/.png;base64,"
            width="400"
            height="337"
            alt="Choice 1"
          />
          <br />
          <strong>New Downtown Townhouse</strong>
          <br />
          <br />
          - $250,000
          <br />
          - 3 bedrooms and 1 bathroom
          <br />
          - 1,850 square feet
          <br />
          - close to public transportation and shopping malls
          <br />
        </div>

        <div id="rightSpeakingPicture" className="wellWhiteBG pointerCurser">
          <img
            src="data:image/.png;base64,"
            width="400"
            height="337"
            alt="Choice 2"
          />
          <br />
          <div id="spacing mt-32 pd-0"></div>
          <strong>Detached Home in Quiet Neighbourhood</strong>
          <br />
          <br />
          - $300,000 <br />
          - 3 bedrooms and 3 bathrooms
          <br />
          - 2,800 square feet
          <br />
          - 30 minutes from downtown; 5 minutes from shops
          <br />
        </div>
      </div>

      <Timer/>
      <br />
      <div id="recordingNote" className="fl-l ml-25">
        <strong>*NOTE:</strong> This sample test is not recording your response.
      </div>
    </div>
  );
}

export default Task5CH;
